.form-control-sm {
  height: calc(1.5em + 0.5rem + 2px);
  padding: 0.25rem 0.5rem;
  font-family: "Acherusgrotesque Regular";
  font-size: 0.875rem;
  line-height: 1.5;
  border-radius: 1.2rem;
}

.form-control-lg {
  height: calc(1.5em + 1rem + 2px);
  padding: 0.5rem 1rem;
  font-family: "Acherusgrotesque Regular";
  font-size: 1rem;
  line-height: 1.5;
  border-radius: 1.3rem;
}

.form-control-xl {
  height: calc(2.5em + 1rem + 3px);
  padding: 0.5rem 1rem;
  font-family: "Acherusgrotesque Regular";
  font-size: 1.125rem;
  line-height: 1.5;
  border-radius: 2.3rem;
}
