.nav-link {
  color: #212529;
  text-decoration: none;
  padding: 10px 0px;
  margin: 0px 5px;
  display: inline-block;
  position: relative;
  opacity: 0.75;
}

.nav-link:hover {
  opacity: 1;
}

.nav-link::before {
  transition: 300ms;
  height: 2px;
  content: "";
  position: absolute;
  background-color: #6610f2;
}

.nav-link-fade-up::before {
  width: 90%;
  bottom: 2px;
  opacity: 0;
}

.nav-link-fade-up:hover::before {
  bottom: 5px;
  opacity: 1;
}

